<template>
  <div id="app">
    <img src="./assets/logo.png" />
    <duo-image-viewer :src="src" @close="handleClose" :srcList="srcList" :showViewer="showViewer" />
    <div>
      <h1 class="description">点击按钮打开 →</h1>
      <button class="btn" @click="handleOpen">查看器</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      src:
        "https://shigongbao.oss-cn-chengdu.aliyuncs.com/pictures/2020-09-08/1599535213868-GDJJZizFVtepBxlXpq.jpg",
      srcList: [
        "https://shigongbao.oss-cn-chengdu.aliyuncs.com/pictures/2020-09-08/1599535213868-GDJJZizFVtepBxlXpq.jpg",
        "https://shigongbao.oss-cn-chengdu.aliyuncs.com/pictures/2020-09-08/1599535221238-tQfrTrrwOLSdhtiVBY.jpg",
      ],
      showViewer: false,
    };
  },
  methods: {
    handleOpen() {
      this.showViewer = !this.showViewer;
    },
    handleClose() {
      this.showViewer = false;
    },
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.description {
  display: inline-block;
  margin-right: 20px;
}
.btn {
  vertical-align: 3px;
}
</style>



